import React, {Component} from "react";
import {OraButton, OraModal} from "eshop/components/OraCommonComponents";
import ModalService from "eshop/flux/utils/OraModalService";

class PickDevicePopup extends Component {

    constructor(props) {
        super(props);
        this.desc = {}
    }

    static uniqId(){
        return "pick-device-popup-hfjfjeur-uniq"
    }

    static initialize(desc) {
        let id = this.uniqId();
        if($("[id*='"+id+"']").length==0){
            this.desc = desc || {}
            ModalService.add((data) => <PickDevicePopup id={id} {...data}/>);
        }
    }

    static open(data) {
        ModalService.open(this.uniqId(), {...this.desc, ...data});
    }

    render() {
        return (
            <OraModal id={ this.props.id } showClose={true} narrow={true} fallback={ this.props.onClickRemove }>
                <div className="l-page-row">
                    <div className="l-row">
                        <div className="l-col l-col-12">
                            <h2>{ this.props.title }</h2>
                        </div>
                    </div>
                    <div className="l-row u-spacing-l">
                        <div className="l-col l-col-12">
                            <p>{this.props.content}</p>
                        </div>
                    </div>
                    <div className="l-row">

                        {/*smartphone view*/}
                        <div className="u-medium-hide u-large-hide ">
                            <div className="l-col l-col-small-12 u-margin-s">
                                <OraButton onClick={this.props.onClickConfirm} className="u-w-100" modal={true}>
                                    {this.props.confirm}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-small-12 ">
                                <OraButton onClick={this.props.onClickDecline} className="u-w-100" modal={true} type="secondary">
                                    {this.props.decline}
                                </OraButton>
                            </div>
                        </div>

                        {/*desktop view*/}
                        <div className="u-small-hide">
                            <div className="l-col l-col-medium-4 l-col-6">
                                <OraButton onClick={this.props.onClickDecline} className="u-w-100" modal={true} type="secondary">
                                    {this.props.decline}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-medium-4 l-col-6">
                                <OraButton onClick={this.props.onClickConfirm} className="u-w-100" modal={true}>
                                    {this.props.confirm}
                                </OraButton>
                            </div>
                        </div>

                    </div>
                </div>
            </OraModal>
        )
    }

}

PickDevicePopup.defaultProps = {
    title: "Czy chcesz dobrać teraz urządzenia?",
    content: "Czy chcesz teraz dobrać urządzenia? Możesz też dodać rzeczy do koszyka i przejść bezpośrednio do niego.",
    confirm: "Dobierz urządzenia",
    decline: "Dodaj tylko SIM do koszyka"
};

export default PickDevicePopup;
